<template>
  <section>
    <h1>Your Files</h1>
    <p>These are the files you have uploaded</p>

    <table v-if="uploadedFiles.length!==0">
      <colgroup>
        <col />
        <col />
        <col />
        <col />
      </colgroup>
      <thead>
        <tr>
          <th>Name</th>
          <th>Size</th>
          <th>Upload Date</th>
          <th>Delete</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="file in uploadedFiles" v-bind:key="file.id">
          <td>{{file.name}}</td>
          <td>{{file.size}} byte</td>
          <td>{{file.uploadDate}}</td>
          <td @click="deleteFile(file.id)" class="delete">
            <img src="delete_button.png" alt="bin" />
          </td>
        </tr>
      </tbody>
    </table>

    <p v-else>
      You haven't load any file yet. Go
      <router-link class="router-link" to="/AddFile">here</router-link> to upload a file
    </p>
  </section>
</template>

<script scoped>
import Axios from "axios";
export default {
  name: "ManageFiles",
  components: undefined,
  methods: {
    deleteFile: function (idFile) {
      let rowToDelete = event.currentTarget.parentElement;
      Axios.delete(
        `${process.env.VUE_APP_APIROOT}uploaderarea/filemanagment/${idFile}`
      )
        .then(() => {
          this.uploadedFiles = this.uploadedFiles.filter(
            (current) => current.id !== idFile
          );
          rowToDelete.remove();
        })
        .catch(() => alert("Non Ã¨ stato possibile eliminare il file"));
    },
  },

  data: function () {
    return {
      uploadedFiles: [],
    };
  },

  created: function () {
    Axios.get(
      `${process.env.VUE_APP_APIROOT}uploaderarea/filemanagment`
    )
      .then((response) => (this.uploadedFiles = response.data))
      .catch((error) => console.log(error));
  },
};
</script>

<style scoped>

table {
  text-align: center;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  display: block;
  overflow: auto;
  max-height: 70vh;
}

table colgroup col {
  width: 300px;
}

img[alt="bin"] {
  height: 30px;
  width: auto;
  border: none;
}

section table tbody td img:hover{
  cursor: pointer;
}

::-webkit-scrollbar {
  display: none;
}

</style>